<template>
	<view>
		<navbar :iSimmersive="false" title-color="#ffffff" background="#1b43c4" :placeholder="true" title="我要签到"></navbar>
		<view class="header">
			<view class="header-warp">
				<view class="info">
					<view class="avatar">
						<image :src="user.avatar|| ''"></image>
					</view>
					<view>{{user.username}}</view>
					<view><text>积分：{{user.point}}</text></view>
				</view>
				<view class="activity-rules" @click="go('sign/log')">积分明细</view>
			</view>
		</view>
		
		<view class="sign-box">
			<view class="sign-wrap clear">
				<view class="sign-title">签到领积分</view>
				<view class="sign-list-box">
					<view class="sign-list-days">
						<view
							v-for="(item,index) in config.list"
							:key="index"
							class="list-item"
							:class="(user.sign_count >= index + 1 ? 'active' : '')"
						>
							<view>
								<text>+{{item.point}}</text>
							</view>
							<view>{{item.name}}</view>
						</view>
					</view>
				</view>
				<view class="sign-btn" @click="send" :class="{'disable': user.is_day}">签到</view>
			</view>
		</view>
		
		<view class="coupon clear" v-if="coupon.length">
			<view class="coupon-wrap clear">
				<view class="coupon-title">积分兑换</view>
				<view class="coupon-list clear">

					<view v-for="(item,index) in coupon"
						 :key="index"
						 class="coupon-item"
						 @click="onCoupon(item.id,index)"
					>
						<view>{{item.name}}</view>
						<view>
							<text>{{item.point}}积分</text>
							<text :class="{ 'active': item.active }">点击兑换</text>
						</view>
						<view>{{item.start_time}} ~ {{item.end_time}}</view>
						<view class="coupon-img"><image :src="$static+'app/coupon-img.png'"></image></view>
					</view>

				</view>
			</view>
		</view>
		
		<authorize v-model="isAuthShow"></authorize>
	</view>
</template>

<script>
	import navbar from "@/components/navbar/navbar";
	import authorize from "@/components/authorize/authorize";
	export default {
		components: {
			navbar,authorize
		},
		data() {
			return {
				isAuthShow: false,
				static: "",
				user: {
					avatar: "",
					username: "",
					point: "0",
					is_day: 0,
					sign_count: 0
				},
				config: {
					list: [
						{name: "1天", point: "1", sort: 0},
						{name: "2天", point: "2", sort: 1},
						{name: "3天", point: "3", sort: 2},
						{name: "4天", point: "4", sort: 3},
						{name: "5天", point: "5", sort: 4},
						{name: "6天", point: "6", sort: 5},
						{name: "7天", point: "15", sort: 6}
					]
				},
				coupon: []
			};
		},
		onLoad() {
			this.static = this.$static;
		},
		onShow() {
			this.$store.dispatch("usersStatus").then(()=>{
				this.isAuthShow = false;
				let users = this.$storage.getJson("users");
				this.user.avatar = users.avatar;
				this.user.username = users.nickname || users.username || users.mobile;
				this.$http.getSignConfig().then((res)=>{
					if(res.status){
						this.user = res.data;
						this.config = res.data.config;
						this.coupon = res.data.coupon;
					}
				});
			}).catch(()=>{
				this.isAuthShow = true;
				this.user.avatar = this.$static + 'avatar.png';
				this.user.username = "请先登录";
				this.user.point = 0;
			});
		},
		methods: {
			send(){
				this.$store.dispatch("usersStatus").then(()=>{
					this.isAuthShow = false;
					if(this.user.is_day){
						return false;
					}
					
					this.$http.sendSign().then(res=>{
						if(res.status){
							this.user.point = res.data.point;
							this.user.sign_count = res.data.sign_count;
							this.user.is_day = 1;
						}else{
							this.$utils.msg(res.info);
						}
					});
				}).catch(()=>{
					this.isAuthShow = true;
				});
			},
			onCoupon(id,index){
				this.$store.dispatch("usersStatus").then(()=>{
					this.isAuthShow = false;
					if(this.coupon[index].active){
						return false;
					}
					
					this.$http.receiveSignCoupon({
						id: id
					}).then(res=>{
						if(res.status){
							this.coupon[index].active = 1;
							this.user.point = res.data;
							this.$utils.msg(res.info);
						}else{
							this.$utils.msg(res.info);
						}
					});
				}).catch(()=>{
					this.isAuthShow = true;
				});
				
				
			},
		    go(value,param){
				this.$store.dispatch("usersStatus").then(()=>{
					this.isAuthShow = false;
					let params = param || "";
					if(params == ""){
						this.$utils.navigateTo(value);
					}else{
						this.$utils.navigateTo(value,params);
					}
				}).catch(()=>{
					this.isAuthShow = true;
				});
		    }
		},
	}
</script>

